*,
*:after,
*:before {
  box-sizing: border-box; }

body {
  font-size: 16px;
  color: #000;
  line-height: 1.25; }
  @media screen and (min-width: 500px) {
    body {
      line-height: 1.375; } }

h1,
h2,
blockquote {
  font-weight: 900; }

h3,
h4 {
  font-weight: 500; }

h1 {
  font-size: 2rem;
  line-height: 1.25; }
  @media screen and (min-width: 640px) {
    h1 {
      font-size: 2.5rem;
      line-height: 1.125; } }
  @media screen and (min-width: 850px) {
    h1 {
      font-size: 3rem;
      line-height: 1.05; } }

h2 {
  font-size: 1.625rem;
  line-height: 1.15384615; }
  @media screen and (min-width: 640px) {
    h2 {
      font-size: 2rem;
      line-height: 1.25; } }
  @media screen and (min-width: 850px) {
    h2 {
      font-size: 2.25rem; } }

h3 {
  font-size: 1.375rem;
  line-height: 1.13636364; }
  @media screen and (min-width: 640px) {
    h3 {
      font-size: 1.5rem;
      line-height: 1.25; } }
  @media screen and (min-width: 850px) {
    h3 {
      font-size: 1.75rem; } }

h4 {
  font-size: 1.125rem;
  line-height: 1.111; }
  @media screen and (min-width: 640px) {
    h4 {
      line-height: 1.222; } }

small,
.itemList__linkFull,
figcaption {
  font-size: .85rem; }

blockquote {
  font-size: 1.25rem;
  line-height: 1.25; }
  @media screen and (min-width: 640px) {
    blockquote {
      font-size: 1.5rem;
      line-height: 1.45833; } }

a {
  display: inline-block;
  font-weight: 500;
  color: #f25405;
  text-decoration: none; }

.u-center {
  margin-right: auto;
  margin-left: auto; }

.u-centerChildren {
  text-align: center; }

.u-blockLink {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  font-size: 0;
  text-indent: 200%;
  white-space: nowrap;
  opacity: 0; }

.u-chunk {
  position: relative; }

.u-chunk::before {
  content: "";
  position: absolute;
  left: calc(-5% * 1.11111);
  display: block;
  width: calc(5% * 1.11111);
  border-top: 6px solid #000; }

.frame {
  margin: 0 5%; }

.frame:after {
  content: "";
  clear: both;
  display: table; }

[class*='bit-'] {
  float: right;
  padding: 0 0.5rem; }
  @media screen and (min-width: 640px) {
    [class*='bit-'] {
      padding: 0 1rem; } }
  [class*='bit-'][class*="--noPad"] {
    padding: 0; }
  [class*='bit-'][class*="--center"] {
    float: none;
    margin: 0 auto; }
  [class*='bit-'][class*="--tile"] {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem; }
    @media screen and (min-width: 640px) {
      [class*='bit-'][class*="--tile"] {
        padding-top: 1rem;
        padding-bottom: 1rem; } }

.bit-1,
.bit-2,
.bit-3,
.bit-4,
.bit-5,
.bit-6,
.bit-7,
.bit-8 {
  width: calc(100% / 10 * 9); }

.bit-9,
.bit-10 {
  width: 100%; }

@media screen and (min-width: 500px) {
  .bit-1 {
    width: calc(100% / 10 * 1); }
  .bit-2 {
    width: calc(100% / 10 * 2); }
  .bit-3 {
    width: calc(100% / 10 * 3); }
  .bit-4 {
    width: calc(100% / 10 * 4); }
  .bit-5 {
    width: calc(100% / 10 * 5); }
  .bit-6 {
    width: calc(100% / 10 * 6); }
  .bit-7 {
    width: calc(100% / 10 * 7); }
  .bit-8 {
    width: calc(100% / 10 * 8); }
  .bit-9 {
    width: calc(100% / 10 * 9); }
  .bit-10 {
    width: 100%; } }

.article__paragraph:not(:last-child) {
  margin-bottom: 1.5em; }

.fullScreenBlock {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: calc(90% * .8);
  height: 60vh;
  padding: 15%;
  font-weight: 400;
  color: #fff;
  background-color: #000; }
  @media screen and (min-width: 640px) {
    .fullScreenBlock {
      font-size: 1.5rem;
      line-height: 1.45833; } }
  @media screen and (min-width: 850px) {
    .fullScreenBlock {
      width: calc(90% * .6);
      padding: 10%; } }

.fullScreenBlock__container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh; }

.itemList {
  position: relative; }
  .itemList__name {
    margin-bottom: 6rem;
    line-height: 1em; }

.itemList__tile {
  position: relative;
  margin-bottom: 3rem;
  transition: all 0.2s ease-in-out; }
  .itemList__tile::after {
    background-color: transparent;
    background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='100%' height='100%'><defs><pattern id='pinstripe' patternUnits='userSpaceOnUse' patternTransform='rotate(45)' width='9' height='9'><line x1='4.5' y='0' x2='4.5' y2='9' stroke='rgba(0, 0, 0, 0.105)' stroke-width='3' /></pattern></defs><rect width='100%' height='100%' fill='url(%23pinstripe)' /></svg>");
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    transition: all 0.2s ease-in-out; }
  .itemList__tile:hover {
    color: #f25405;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.025);
    transform: translateX(-10px) translateY(-10px);
    transition: all 0.2s ease-in-out; }
  .itemList__tile:hover::after {
    transform: translateX(10px) translateY(10px);
    transition: all 0.2s ease-in-out; }

.itemList__item {
  padding: 0.6rem;
  background: #fff; }
  @media screen and (min-width: 640px) {
    .itemList__item {
      padding: 1rem; } }
  .itemList__item__title {
    color: #f25405;
    text-transform: capitalize; }
  .itemList__item__subtitle {
    margin-bottom: .75em;
    font-weight: 500;
    color: #a0a4a3; }

.itemList__linkFull {
  padding: .5rem;
  text-align: right;
  border-top: 3px solid #000; }
